.slide-1 {
  /* background-image: linear-gradient( 134.6deg,  rgba(201,37,107,1) 15.4%, rgba(116,16,124,1) 74.7% ); */
  background-image: radial-gradient(circle farthest-corner at 83.7% 4.3%, rgba(173, 0, 171, 1) 0%, rgba(15, 51, 92, 1) 90%);
}

.slide-1-wave-container {
  position: absolute;
  left: -2px;
  width: 101%;
  top: -22vw;
  -webkit-filter: drop-shadow(0px -9px 43px 0px rgba(0, 0, 0, 0.93));
          filter: drop-shadow(0px -9px 43px 0px rgba(0, 0, 0, 0.93));
}


@-webkit-keyframes fadeInAnimation {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-10%) scale(.9);
            transform: translateY(-10%) scale(.9);
  }

  20% {
    opacity: 0;
    -webkit-transform: translateY(-2%) scale(.9);
            transform: translateY(-2%) scale(.9);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0) scale(1);
            transform: translateY(0) scale(1);
  }
}


@keyframes fadeInAnimation {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-10%) scale(.9);
            transform: translateY(-10%) scale(.9);
  }

  20% {
    opacity: 0;
    -webkit-transform: translateY(-2%) scale(.9);
            transform: translateY(-2%) scale(.9);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0) scale(1);
            transform: translateY(0) scale(1);
  }
}

